<template>
  <div id="loginView">
    <div class="align-center">
      <img alt="Vue logo" src="@/assets/Admin/images/login/sg_logo.png" />

      <table class="table">
        <tr>
          <td></td>
          <td><input id="id" v-model="id" type="text" /></td>
        </tr>
        <tr>
          <td></td>
          <td><input id="pw" v-model="pw" type="text" /></td>
        </tr>
      </table>

      <button @click="loginClick" type="button"></button>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import request from "request"

// proxy  
var protocol = location.protocol;
var hostName = location.hostname;
var port = location.port;
var url = protocol + "//" + hostName + ":" + port;

console.log("### login test");
console.log(url);

export default {
  name: "loginView",
  data: function() {
    return {
      id: "",
      pw: "",
    };
  },
  methods: {
    loginClick: function() {
      console.log("### id : " + this.id);
      console.log("### pw : " + this.pw);


      axios
        .post("/admin/login", {
          id: this.id,
          pw: this.pw,
        })
        .then(response => {
          if (response.data.result) {
            // admin page 
            this.$router.push("/admin");
          } else {
            alert(response.data.message);
          }
        });
    },
  },
  mounted() {
    request('http://localhost:8080/login', function (body) {
      window.console.log("body: ", body);
    });
  }
};
</script>

<style scoped>
#loginView .align-center {
  text-align: center;
}

#loginView table {
  border: 1px solid #444444;
  margin-left: auto;
  margin-right: auto;
}

#loginView th,
td {
  border: 1px solid #444444;
}
</style>
